angular
	.module("baoNguyenApp", [
		"common.fabric",
		"common.fabric.utilities",
		"common.fabric.constants",
		'colorpicker.module'
	])
	.controller("nguyenCtrl", [
		"$scope",
		"Fabric",
		"FabricConstants",
		"Keypress",
		function ($scope, Fabric, FabricConstants, Keypress) {
			$scope.fabric = {};
			$scope.FabricConstants = FabricConstants;
			
			
			$scope.saveImage = function () {
				$scope.fabric.download('nguyen_' +Math.random())
			};

			//
			// Creating Canvas Objects
			// ================================================================
			// $scope.addShape = function (path) {
			// 	$scope.fabric.addShape("http://fabricjs.com/assets/15.svg");
			// };

			// $scope.addImage = function (image) {
			// 	$scope.fabric.addImage("./img/daniel-season-nine.jpg");
			// };

			$scope.addImageUpload = function (data) {
				var obj = angular.fromJson(data);
				$scope.addImage(obj.filename);
			};

			//
			// Editing Canvas Size
			// ================================================================
			$scope.selectCanvas = function () {
				$scope.canvasCopy = {
					width: $scope.fabric.canvasOriginalWidth,
					height: $scope.fabric.canvasOriginalHeight
				};
			};

			$scope.setCanvasSize = function () {
				$scope.fabric.setCanvasSize(
					$scope.canvasCopy.width,
					$scope.canvasCopy.height
				);
				$scope.fabric.setDirty(true);
				// delete $scope.canvasCopy;
			};

			//
			// Init
			// ================================================================
			$scope.init = function () {
				$scope.fabric = new Fabric({
					JSONExportProperties: FabricConstants.JSONExportProperties,
					textDefaults: FabricConstants.textDefaults,
					shapeDefaults: FabricConstants.shapeDefaults,
					json: {}
				});
			};

			$scope.$on("canvas:created", $scope.init);

			Keypress.onSave(function () {
				$scope.updatePage();
			});
			// ================================================================
			// var desiredHeight = 300;
			// var desiredWidth = 770;
			// var myImageData = document.getElementById("fileData");
			// var canvas = document.getElementById("myCanvas");
			// var canvas = new fabric.Canvas("myCanvas");

			// var rect = new fabric.Rect({
			// 	fill: "lightgray",
			// 	left: $scope.fabric.width / 2,
			// 	top: $scope.fabric.height / 2,
			// 	width: desiredWidth,
			// 	height: desiredHeight,
			// 	stroke: "darkgray",
			// 	strokeDashArray: [5, 5],
			// 	selectable: false
			// });
			// $scope.fabric.add(rect);
			// var clipZone = {
			// 	y: rect.top - 0.5 * rect.height,
			// 	x: rect.left - 0.5 * rect.width,
			// 	width: desiredWidth,
			// 	height: desiredHeight,
			// 	quality: 1
			// };

			$scope.caption = "";
			$scope.fileUrl = "";

			$scope.imageChange = function (inp) {
				// console.log("imageChange");
				file = inp.files[0];
				fr = new FileReader();
				fr.onload = createImage;
				fr.readAsDataURL(file);

				var img = null;

				function createImage() {
					// console.log("createImage");
					img = new Image();
					img.onload = imageLoaded;
					img.src = fr.result;
				}

				function imageLoaded() {
					// console.log("imageLoaded");
					var fabImg = new fabric.Image(img);

					// fabImg.scale(1.0).set({
					// 	left: canvas.width / 2,
					// 	top: canvas.height / 2
					// });

					// canvas.add(fabImg);
					// canvas.setActiveObject(fabImg);
					// fabImg.scale(1.0).set({
					// 	left: 100,
					// 	top: 100
					// });
					// console.log(img)
					$scope.fabric.addImage(img.src);
					// $scope.fabric.setActiveObject(img.src);
				}
			};

			// $scope.submit = function (event) { };

			// $scope.capture = function () {
			// 	console.log("capture");
			// 	console.log($scope.file);
			// 	var active = canvas.getActiveObject();
			// 	canvas.deactivateAll();

			// 	var data = canvas.toDataURLWithCropping("png", clipZone);

			// 	myImageData.value = data;
			// 	myImage.src = data;
			// 	canvas.setActiveObject(active);
			// };
		}
	]);

////////////

var invokeImageChange = function (that) {
	angular
		.element(that)
		.scope()
		.imageChange(that);
	angular
		.element(that)
		.scope()
		.$digest();
};



